.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  overflow: hidden; }

.container {
  width: 100%;
  min-width: 1024px;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (max-width: 1023px) {
    .container {
      min-width: 768px;
      max-width: 1023px;
      padding: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      min-width: 320px;
      padding: 0 15px; } }

.page-body {
  display: grid;
  min-height: 100%;
  overflow-x: hidden;
  grid-template-rows: min-content 1fr min-content; }

.page {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  width: auto;
  height: 100%;
  margin: 0 auto;
  padding: 0; }

a {
  color: inherit;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.header {
  width: 100%;
  margin: 0;
  padding-top: 30px;
  padding-bottom: 9px;
  font-style: normal;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif;
  color: #1d1613; }
  @media (max-width: 1023px) {
    .header {
      padding-bottom: 30px; } }
  @media (max-width: 767px) {
    .header {
      padding-top: 20px;
      padding-bottom: 0; } }

@media (max-width: 1023px) {
  .header.is-js {
    position: absolute;
    z-index: 20;
    overflow-y: auto;
    background-color: #a87b62; } }

.header__upper-block {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 19px;
  font-size: 14px;
  line-height: 21px; }
  @media (max-width: 1023px) {
    .header__upper-block {
      align-items: center;
      padding-bottom: 0; } }

.header__lower-block {
  padding-top: 7px;
  border-top: 1px solid #f6f5f4; }
  @media (max-width: 1023px) {
    .header__lower-block {
      padding-top: 0;
      padding-bottom: 29px;
      border-top: none; } }

@media (max-width: 1023px) {
  .header__lower-block.block-close {
    display: none; } }

.header__toggle-menu {
  display: none; }
  @media (max-width: 1023px) {
    .header__toggle-menu {
      display: flex;
      align-self: center;
      width: 25px;
      height: 18px;
      padding: 0;
      background-color: transparent;
      background-image: url("../img/svg/open-menu.svg");
      border: none;
      cursor: pointer; } }

.header__toggle-menu.no-js {
  opacity: 0; }

.header__form-search {
  display: flex;
  order: -1;
  width: 174px;
  margin-top: 9px; }
  @media (max-width: 1023px) {
    .header__form-search {
      order: 0;
      width: 100%;
      margin-top: 40px; } }

@media (max-width: 1023px) {
  .header__form-search.block-close {
    display: none; } }

.header__form-search form {
  display: flex;
  align-items: flex-start;
  width: 100%; }
  @media (max-width: 1023px) {
    .header__form-search form {
      padding: 7px;
      background: rgba(0, 0, 0, 0.15);
      border-radius: 21px; } }

.header__form-search button {
  display: flex;
  padding: 5px 10px 5px 0;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer; }
  .header__form-search button:hover {
    color: #a87b62; }
  .header__form-search button:active {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .header__form-search button {
      padding: 5px; }
      .header__form-search button svg {
        width: 16px;
        height: 16px; } }

.header__form-search label {
  display: flex;
  align-items: center;
  width: 100%; }
  .header__form-search label:hover {
    color: #a87b62; }

.header__form-search input {
  width: 150px;
  padding: 5px;
  border: none; }
  .header__form-search input::placeholder {
    color: #9b9997; }
    @media (max-width: 1023px) {
      .header__form-search input::placeholder {
        color: transparent; } }
  .header__form-search input:hover, .header__form-search input:focus {
    border-radius: 3px;
    outline: solid 2px #a87b62; }
  @media (max-width: 1023px) {
    .header__form-search input {
      width: 100%;
      background-color: transparent;
      border-radius: 15px; }
      .header__form-search input:focus {
        border-radius: 15px; } }

.header__logo {
  display: flex;
  justify-self: center; }
  @media (max-width: 767px) {
    .header__logo {
      width: 52px;
      height: 52px; } }

.header__logo--login {
  width: 66px;
  margin: 0 auto;
  margin-bottom: 20px; }

.main-nav {
  display: flex;
  justify-content: center;
  font-size: 16px;
  line-height: 24px; }
  @media (max-width: 1023px) {
    .main-nav {
      flex-direction: column;
      justify-content: flex-start;
      margin-top: 44px;
      font-size: 24px;
      line-height: 24px; } }

.main-nav__list {
  display: grid;
  margin: 0;
  margin-left: 10px;
  padding: 0;
  list-style: none;
  grid-gap: 15px;
  grid-template-columns: repeat(5, min-content); }
  @media (max-width: 1023px) {
    .main-nav__list {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-left: 0; } }

.main-nav__additional-list {
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0;
  padding-top: 28px;
  list-style: none;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  font-family: "Poppins", "Arial", sans-serif;
  color: rgba(0, 0, 0, 0.75); }
  @media (max-width: 1023px) {
    .main-nav__additional-list {
      display: flex; } }

.main-nav__item {
  display: flex;
  justify-self: center; }
  .main-nav__item:last-child {
    margin-right: 0; }

.main-nav__additional-item {
  display: flex; }

.main-nav__link {
  display: flex;
  padding: 10px; }
  .main-nav__link:hover {
    color: #a87b62; }
  .main-nav__link:active {
    text-decoration: underline; }
  @media (max-width: 1023px) {
    .main-nav__link {
      padding: 9px 0; } }

.main-nav__additional-link {
  display: flex;
  padding: 10px 0; }
  .main-nav__additional-link:hover {
    color: #a87b62; }
  .main-nav__additional-link:active {
    text-decoration: underline; }

.main-nav__additional-link-login {
  margin: 24px 0;
  color: #000000; }

.user-nav {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 7px;
  width: 174px; }
  @media (max-width: 1023px) {
    .user-nav {
      align-items: center;
      width: auto;
      margin-top: 0; } }

.user-nav__link {
  display: flex;
  padding: 5px 0; }
  .user-nav__link:hover {
    color: #a87b62; }
  .user-nav__link:active {
    text-decoration: underline; }
  @media (max-width: 1023px) {
    .user-nav__link {
      padding: 0;
      margin-left: 5px; } }

.user-nav__link-login {
  margin-right: 37px; }
  @media (max-width: 1023px) {
    .user-nav__link-login {
      display: none; } }

@media (max-width: 1023px) {
  .user-nav__link-cart span {
    display: none; } }

.user-nav__link-cart svg {
  display: none; }
  @media (max-width: 1023px) {
    .user-nav__link-cart svg {
      display: flex;
      width: 20px;
      height: 18px; } }
